
.change-cover {
  height: 100%;
  border-radius: 5px;
  overflow: hidden;
  background: #fff;
}
.change-cover .modal-head {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 5px;
    background: #2196F3;
    color: #fff;
}
.change-cover .cropper {
    height: 370px !important;
}
.change-cover .modal-bottom {
    padding: 10px;
    border-top: 1px solid #cfd0d4;
    text-align: end;
}
.change-cover .modal-bottom button {
      border: none;
      outline: none;
      background: #cfd0d4;
      width: 75px;
      height: 30px;
      color: #fff;
      border-radius: 5px;
      cursor: pointer;
}
.change-cover .modal-bottom .confirm {
      background: #2196F3;
}



