
@charset "UTF-8";
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: '微软雅黑';
  -webkit-font-smoothing: antialiased;
  -webkit-user-select: none;
}
.iconfont {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  display: inline-block;
  font-size: 16px;
}
.iconfont:hover {
    cursor: pointer;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



